<template>
  <div>
    <el-card class="box-card">
      <el-col :span="24">
        <el-row>
          <el-col :span="24">
            <el-form :inline="true" :model="formInline" class="demo-form-inline">
              <el-form-item label="搜索">
                <el-input v-model="formInline.value1" clearable placeholder="根据流水号/个人信息查询"></el-input>
              </el-form-item>
              <el-form-item label="日期选择">
                <el-date-picker v-model="formInline.value2" @change="timeClick" value-format="yyyy-MM-dd" type="daterange" range-separator="至" start-placeholder="开始日期"
                  end-placeholder="结束日期">
                </el-date-picker>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="onSubmit">查询</el-button>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="openVisible5">体检信息记录下载</el-button>
              </el-form-item>
            </el-form>
          </el-col>
          <el-col :span="24" style="margin-top:10px;">
            <el-table :data="tableData" border onselectstart="return false">
              <el-table-column width="130" prop="BusinessNo" label="流水号" align="center"></el-table-column>
              <el-table-column width="80" prop="IDCardName" label="个人信息" align="center">
                <template slot-scope="scope">
                  <el-popover trigger="hover" placement="top">
                    <p style="font-size:17px;color:#000;">个人信息</p>
                    <p>姓名: {{ scope.row.IDCardName }}</p>
                    <p>性别: {{ scope.row.IDCardSex }}</p>
                    <p>证件类型: {{ scope.row.CardType }}</p>
                    <p>证件号码: {{ scope.row.IDCardNo }}</p>
                    
                    <p>联系电话: {{ scope.row.PhoneNo }}</p>

                    <!-- <p v-if="tableData.PhoneNo&&ROLEID == 1">联系电话: {{ scope.row.PhoneNo }}</p>
                    <p v-if="tableData.PhoneNo&&ROLEID != 1">联系电话: {{ scope.row.PhoneNo | msgPhone }}</p> -->
                    <p>登记住所详细地址: {{ scope.row.IDCardAddress }}</p>
                    <div slot="reference" class="name-wrapper">
                      <el-tag size="medium">{{ scope.row.IDCardName }}</el-tag>
                    </div>
                  </el-popover>
                </template>
              </el-table-column>
              <el-table-column width="100" label="操作信息" align="center">
                <template slot-scope="scope">
                  <el-popover trigger="hover" placement="top">
                    <p style="font-size:17px;color:#000;">操作信息</p>
                    <p>体检医院名称: {{ scope.row.hospitalName }}</p>
                    <p>体检机设备编号: {{ scope.row.DeviceID }}</p>
                    <p>审核操作人员: {{ scope.row.AuditUserName }}</p>
                    <div slot="reference" class="name-wrapper">
                      <el-tag size="medium">悬停查看</el-tag>
                    </div>
                  </el-popover>
                </template>
              </el-table-column>
              <el-table-column width="120" label="医院审核" align="center">
                <template slot-scope="scope">
                  <el-tag v-if="scope.row.AuditStatus == 1" type="success" disable-transitions>审核通过</el-tag>
                  <el-tag v-if="scope.row.AuditStatus == -1" type="danger" disable-transitions>审核不通过</el-tag>
                  <el-tag v-if="scope.row.AuditStatus == 0" type="info" disable-transitions>未审核</el-tag>
                </template>
              </el-table-column>
              <el-table-column width="80" label="体检缴费" align="center">
                <template slot-scope="scope">
                  <i :class="scope.row.examinationCost === '已缴费' ? 'el-icon-success' : 'el-icon-error'"></i>
                </template>
              </el-table-column>
              <el-table-column width="80" label="上传结果" align="center">
                <template slot-scope="scope">
                  <el-popover trigger="hover" placement="top">
                    <p style="font-size:17px;color:#000;">详情</p>
                    <p>代号: {{ scope.row.uploadDh }}</p>
                    <p>说明: {{ scope.row.uploadExplain }}</p>
                    <div slot="reference" class="name-wrapper">
                      <el-tag :type="scope.row.uploadState === '成功' ? 'success' : 'danger'" disable-transitions>{{scope.row.uploadState}}</el-tag>
                    </div>
                  </el-popover>
                </template>
              </el-table-column>
              <el-table-column width="180" prop="ExamineTime" label="体检日期" align="center"></el-table-column>
              <el-table-column width="180" prop="AuditTime" label="审核时间" align="center"></el-table-column>
              <el-table-column width="100" fixed="right" label="操作" align="center">
                <template slot-scope="scope">
                  <el-tooltip content="详情" placement="top">
                    <el-button @click="openVisible2(scope.$index, scope.row)" type="primary" icon="el-icon-s-order"
                      circle></el-button>
                  </el-tooltip>
                  <!-- <el-tooltip content="补传" placement="top">
                    <el-button @click="SeeClick(scope.$index, scope.row)" type="success" icon="el-icon-upload2" circle></el-button>
                  </el-tooltip>
                  <el-tooltip content="移入黑名单" placement="top">
                    <el-button @click="openVisible(scope.$index, scope.row)" type="danger" icon="el-icon-heimingdan"
                      circle></el-button>
                  </el-tooltip> -->
                </template>
              </el-table-column>
            </el-table>
            <el-pagination
              background
              layout="prev, pager, next ,sizes"
              :total="TotalCount"
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              style="margin-top:10px;"
            ></el-pagination>
          </el-col>
        </el-row>
      </el-col>
    </el-card>
    <!-- 移入黑名单 -->
    <el-dialog title="移入黑名单" :visible.sync="dialogVisible">
      <div>姓名：{{SeeClickData.name}}</div>
      <div>证件类型：{{SeeClickData.type}}</div>
      <div>证件号码：{{SeeClickData.idcard}}</div>
      <el-form>
        <el-form-item label="拉黑原因:">
          <el-input type="textarea" v-model="reason"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogVisible = false">确 认</el-button>
      </div>
    </el-dialog>
    <!-- 信息下载 -->
    <el-dialog width="40%" title="下载筛选" :visible.sync="dialogVisible5">
      <el-form label-width="100px">
          <el-form-item label="上传结果">
            <el-select v-model="downLoad.value1">
              <el-option label="成功" value="成功"></el-option>
              <el-option label="失败" value="失败"></el-option>
              <el-option label="未上传" value="未上传"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="日期选择">
            <el-date-picker v-model="downLoad.value2" type="daterange" range-separator="至" start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
        <el-col :span="15">
          <el-form-item label="审核人员">
            <el-input v-model="downLoad.value3" placeholder="选填"></el-input>
          </el-form-item>
        </el-col>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible5 = false">取 消</el-button>
        <el-button type="primary" @click="dialogVisible5 = false">下 载</el-button>
      </div>
    </el-dialog>
    <!-- 详情弹框 -->
    <el-dialog title="体检详情" :visible.sync="dialogVisible2" center :fullscreen="true">
      <el-row :gutter="10">
        <el-col :span="8">
          <el-card>
            <div class="boxTopTitle">人员信息</div>
            <ul class="topBoxs">
              <li class="boxss" @click="openVisible3">
                <div>
                  <img :src="SeeClickData.ExamineIMG" alt="图片加载失败">
                </div>
                <p class="demonstrations">体检拍照</p>
              </li>
              <li class="boxss" @click="openVisible3">
                <div>
                  <img :src="SeeClickData.IDCardIMG" alt="图片加载失败">
                </div>
                <p class="demonstrations">身份证照片</p>
              </li>
            </ul>
            <div class="textItem"></div>
            <div class="textItem">
              <div>办理业务类型：</div>
              <div>{{SeeClickData.BusinessType}}</div>
            </div>
            <div class="textItem">
              <div>流水号：</div>
              <div>{{SeeClickData.BusinessNo}}</div>
            </div>
            <div class="textItem">
              <div>车型：</div>
              <div class="textCar">{{SeeClickData.CarType}}</div>
            </div>
            <div class="textItem">
              <div>姓名：</div>
              <div>{{SeeClickData.IDCardName}}</div>
            </div>
            <div class="textItem">
              <div>性别：</div>
              <div>{{SeeClickData.IDCardSex}}</div>
            </div>
            <div class="textItem">
              <div>证件类型：</div>
              <div>{{SeeClickData.CardType}}</div>
            </div>
            <div class="textItem">
              <div>证件号码：</div>
              <div>{{SeeClickData.IDCardNo}}</div>
            </div>
            <div class="textItem">
              <div>联系地址：</div>
              <div>{{SeeClickData.IDCardAddress}}</div>
            </div>
            <div class="textItem">
              <div>联系电话：</div>
              <div v-if="SeeClickData.PhoneNo&&ROLEID != '1'">{{SeeClickData.PhoneNo|msgPhone}}</div>
              <div v-if="SeeClickData.PhoneNo&&ROLEID == '1'">{{SeeClickData.PhoneNo}}</div>
            </div>
            <div :class="SeeClickData.carState=='审核通过'?'textBot':'textBot1'">{{SeeClickData.carState}}</div>
          </el-card>
        </el-col>
        <el-col :span="16">
          <el-card>
            <div class="boxTopTitle">体检项目</div>
            <el-row :gutter="10">
              <el-col :span="6">
                <p class="pictureText">躯干和颈部</p>
                <div class="picture" @click="openVisible4(0)">
                  <img :src="SeeClickData.QGIMG" alt="图片加载失败">
                </div>
              </el-col>
              <el-col :span="6">
                <p class="pictureText">视力左</p>
                <div class="picture" @click="openVisible4(1)">
                  <img :src="SeeClickData.SLZIMG" alt="图片加载失败">
                </div>
              </el-col>
              <el-col :span="6">
                <p class="pictureText">视力右</p>
                <div class="picture" @click="openVisible4(2)">
                  <img :src="SeeClickData.SLYIMG" alt="图片加载失败">
                </div>
              </el-col>
              <el-col :span="6">
                <p class="pictureText">辨色力</p>
                <div class="picture" @click="openVisible4(3)">
                  <img :src="SeeClickData.BSLIMG" alt="图片加载失败">
                </div>
              </el-col>
            </el-row>
            <el-row :gutter="10">
              <el-col :span="6">
                <p class="pictureText">听力</p>
                <div class="picture" @click="openVisible4(4)">
                  <img :src="SeeClickData.TLIMG" alt="图片加载失败">
                </div>
              </el-col>
              <el-col :span="6">
                <p class="pictureText">上肢（左）</p>
                <div class="picture" @click="openVisible4(8)">
                  <img :src="SeeClickData.SZZIMG" alt="图片加载失败">
                </div>
              </el-col>
              <el-col :span="6">
                <p class="pictureText">上肢（右）</p>
                <div class="picture" @click="openVisible4(9)">
                  <img :src="SeeClickData.SZYIMG" alt="图片加载失败">
                </div>
              </el-col>
              <el-col :span="6">
                <p class="pictureText">身高</p>
                <div class="picture" @click="openVisible4(6)">
                  <img :src="SeeClickData.SGIMG" alt="图片加载失败">
                </div>
              </el-col>
            </el-row>
            <el-row :gutter="10">
              <el-col :span="6">
                <p class="pictureText">腿部</p>
                <div class="picture" @click="openVisible4(7)">
                  <img :src="SeeClickData.XZIMG" alt="图片加载失败">
                </div>
              </el-col>
            </el-row>
          </el-card>
        </el-col>
      </el-row>
      <el-dialog width="30%" title="照片详情" :visible.sync="dialogVisible3" append-to-body>
        <el-carousel indicator-position="none" :autoplay="false">
          <el-carousel-item>
            <img class="openImage2" :src="SeeClickData.tjPhoto" alt="图片加载失败" title="体检拍照">
          </el-carousel-item>
          <el-carousel-item>
            <img class="openImage2" :src="SeeClickData.idPhoto" alt="图片加载失败" title="身份证照片">
          </el-carousel-item>
        </el-carousel>
      </el-dialog>
      <el-dialog title="照片详情" :visible.sync="dialogVisible4" append-to-body>
        <el-carousel indicator-position="none" :autoplay="false">
          <el-carousel-item>
              <img class="openImage" :src="SeeClickData.QGIMG" alt="图片加载失败" title="颈部">
          </el-carousel-item>
          <el-carousel-item>
              <img class="openImage" :src="SeeClickData.SLZIMG" alt="图片加载失败" title="视力左">
          </el-carousel-item>
          <el-carousel-item>
              <img class="openImage" :src="SeeClickData.SLYIMG" alt="图片加载失败" title="视力右">
          </el-carousel-item>
          <el-carousel-item>
              <img class="openImage" :src="SeeClickData.BSLIMG" alt="图片加载失败" title="辨色力">
          </el-carousel-item>
          <el-carousel-item>
              <img class="openImage" :src="SeeClickData.TLIMG" alt="图片加载失败" title="听力">
          </el-carousel-item>
          <el-carousel-item>
              <img class="openImage" :src="SeeClickData.SZZIMG" alt="图片加载失败" title="上肢左">
          </el-carousel-item>
          <el-carousel-item>
              <img class="openImage" :src="SeeClickData.SZYIMG" alt="图片加载失败" title="上肢右">
          </el-carousel-item>
          <el-carousel-item>
              <img class="openImage" :src="SeeClickData.SGIMG" alt="图片加载失败" title="身高">
          </el-carousel-item>
          <el-carousel-item>
              <img class="openImage" :src="SeeClickData.XZIMG" alt="图片加载失败" title="腿部">
          </el-carousel-item>
        </el-carousel>
      </el-dialog>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible2 = false">确 认</el-button>
      </div>
    </el-dialog>
  </div>
</template>


<script>
const axios = require("axios");
import Qs from 'qs';
  export default {
    data() {
      return {
        ROLEID:1,
        SeeClickData: '',
        reason: '',
        formInline: {
          value1: '',
          value2: ''
        },
        downLoad: {
          value1: '',
          value2: '',
          value3: ''
        },
        dialogVisible: false,
        dialogVisible2: false,
        dialogVisible3: false,
        dialogVisible4: false,
        dialogVisible5: false,
        tableData: [],
        pickerOptions: {
          shortcuts: [{
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        Page:1,
        PageSize:10,
        UserName:'',
        SessionKey:'',
        TotalCount:'',
        EndTime:'',
        StartTime:''
      }
    },
    methods: {
      //打开弹窗
      openVisible(tab, event) {
        this.SeeClickData = event;
        this.dialogVisible = true
      },
      openVisible2(tab, event) {
        this.SeeClickData = event;
        this.dialogVisible2 = true
      },
      openVisible3(tab, event) {
        this.dialogVisible3 = true
      },
      openVisible4(tab) {
        this.dialogVisible4 = true
        this.bannerIndex = tab
      },
      openVisible5() {
        this.dialogVisible5 = true
      },
      SeeClick(tab, event) {
        this.SeeClickData = event;
        console.log(this.SeeClickData)
      },
      //打印
      printContent() {
        this.$print(this.$refs.print)
      },
      //多选框变化
      handleSelectionChange(val) {
        this.multipleSelection = val;
        console.log(this.multipleSelection)
      },
      //查询
      onSubmit() {
        this.getData()
      },
      //时间筛选
      timeClick(){
        this.StartTime = this.formInline.value2[0]
        this.EndTime = this.formInline.value2[1]
        this.getData()
      },
      //分页
      handleSizeChange(val) {
        this.PageSize = val
        this.getData()
      },
      handleCurrentChange(val) {
        this.Page = val
        this.getData()
      },
      //获取列表
      getData(){
          axios({
            method: "post",
            url: http+"api/ExamineWeb/GetExamineList",
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            data: Qs.stringify({
                Page: this.Page,
                PageSize: this.PageSize,
                QueryWhere:this.formInline.value1,
                UserName:this.UserName,
                SessionKey:this.SessionKey,
                StartTime:this.StartTime,
                EndTime:this.EndTime
            })
        })
        .then(res => {
            console.log(res);
            if (res.data.IsSuccess) {
                this.TotalCount = res.data.TotalCount
                this.tableData = res.data.Data
            }
        })
        .catch(error => {
            this.$message({
                showClose: true,
                message: "服务器错误",
                type: "error"
            });
        });
      }
    },
    mounted(){
      this.UserName = sessionStorage.getItem("UserName")
      this.SessionKey = sessionStorage.getItem("SessionKey")
      this.getData()
    },
    watch:{
      "formInline": {
        handler: (val, olVal) => {
          console.log(val)
          console.log(olVal)
        },
        deep:true
      }
    }
  };
</script>


